@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,400;0,600;0,700;0,800;1,700&display=swap');

* {
  font-family: 'Nunito Sans';
}

.title{
  padding-bottom: 20px;
}

h1, h2, h3, h4{
  text-align: center;
}

.game {
  display: flex;
  justify-content: center;
}

.computer {
  border: 0.5px solid  rgb(221, 221, 221);
  width: 300px;
  height: 350px;
  margin-right: 20px;
  background-color: #f5cdd3;
}

.computer h1 {
  font-size: 70px;
  color: #f47b8f;
}

.human {
  border: 0.5px solid rgb(221, 221, 221);
  width: 300px;
  height: 350px;  
  background-color: #fff;
}

.nextRound {
  padding-bottom: 50px;
  padding-top: 30px;
}

.nextRound h3 {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
  border: 0.5px solid rgb(221, 221, 221);
  height: 60px;
  width: 175px;
  background-color: #ededed;
}

.rules {
  display: flex;
  justify-content: space-evenly;
  background-color: #a1dcd8;
  height: 125px;
  align-items: center;
}
.box {
  padding-bottom: 20px;
  padding-top: 5px;
}


.box h1 {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100px;
  height: 100px;
  margin: 0 auto;
  border: 0.5px solid rgb(221, 221, 221);
  
}

.control {
  display: flex;
  justify-content: center;
}

.makeGuess {
  padding-top: 15px;
  padding-bottom: 20px;
}

.makeGuess h4{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border: 0.5px solid rgb(221, 221, 221);
  width: 170px;
  height: 55px;
  margin: 0 auto;
  background-color: #f47b8f;
  color: #fff;
}

.winner {
  display: flex;
  justify-content: center;
  color: #51aeae;
  font-size: 20px;
}